// pages/messages.js
// Next.js page (React) — Professional Chat UI for Trust Market
// Features: Conversations (Only Buyer ↔ Admin or Seller ↔ Admin — No other users can chat) list, Chat window, Composer with attachments (image/video/file),
// voice recording, call / sms / admin connect buttons (placeholders), responsive layout
// Tailwind CSS utility classes used. Replace API placeholders with your backend routes.

import React, { useEffect, useRef, useState } from "react";
import Link from "next/link";
import { Phone, MessageCircle, ImageIcon, FileText, Paperclip, Send, User, Trash2, Video, Mic } from "lucide-react";

// Helper: readable file size
const readableSize = (size) => {
  if (!size) return "0 B";
  const i = Math.floor(Math.log(size) / Math.log(1024));
  return (size / Math.pow(1024, i)).toFixed(2) * 1 + " " + ["B", "KB", "MB", "GB"][i];
};

export default function MessagesPage() {
  // conversations (left list)
  const [conversations, setConversations] = useState([]);
  const [activeConvId, setActiveConvId] = useState(null);
  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [attachments, setAttachments] = useState([]); // { file, preview, type }
  const [recording, setRecording] = useState(false);
  const mediaRecorderRef = useRef(null);
  const recordedChunksRef = useRef([]);
  const messagesEndRef = useRef(null);
  const [loadingConvs, setLoadingConvs] = useState(true);
  const [isConnectingAdmin, setIsConnectingAdmin] = useState(false);

  // Mock: load conversations
  useEffect(() => {
    // Replace with API GET /api/conversations
    const mock = [
      { id: 'conv_admin', name: 'Trust chat', last: 'Support available', unread: 0 },
    ];
    setTimeout(() => { setConversations(mock); setLoadingConvs(false); setActiveConvId(mock[0].id); }, 300);
  }, []);

  // load messages when activeConvId changes
  useEffect(() => {
    if (!activeConvId) return;
    // Replace with API: GET /api/conversations/:id/messages
    setMessages([]);
    // mock messages
    const mockMsgs = [
      { id: 'm1', from: 'other', text: 'Hello, is this still available?', time: '11:03' },
      { id: 'm2', from: 'me', text: 'Yes. Price 2500 BDT. Want to chat?', time: '11:05' },
    ];
    setTimeout(() => setMessages(mockMsgs), 200);
  }, [activeConvId]);

  useEffect(() => {
    // scroll to bottom
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Attach files
  const fileInputRef = useRef(null);
  const handleFiles = (files) => {
    const newAtts = [];
    Array.from(files).forEach((file) => {
      const type = file.type.startsWith('image/') ? 'image' : file.type.startsWith('video/') ? 'video' : 'file';
      const preview = type === 'file' ? null : URL.createObjectURL(file);
      newAtts.push({ id: Date.now() + Math.random(), file, preview, type });
    });
    setAttachments(prev => [...prev, ...newAtts]);
  };

  const removeAttachment = (id) => {
    setAttachments(prev => prev.filter(a => a.id !== id));
  };

  // Send message (text + attachments)
  const sendMessage = async () => {
    if (!text && attachments.length === 0) return;
    // Prepare form data
    const formData = new FormData();
    formData.append('text', text);
    formData.append('conversationId', activeConvId);
    attachments.forEach((a, idx) => formData.append('files', a.file));

    // Replace with API POST /api/messages
    // await fetch('/api/messages', { method: 'POST', body: formData });

    // For demo, append locally
    const newMsg = { id: 'm' + Date.now(), from: 'me', text, time: new Date().toLocaleTimeString([], {hour: '2-digit', minute:'2-digit'}), attachments: attachments.map(a => ({ type: a.type, preview: a.preview, name: a.file.name })) };
    setMessages(prev => [...prev, newMsg]);
    setText('');
    setAttachments([]);
  };

  // Voice recording
  const startRecording = async () => {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      alert('Your browser does not support voice recording');
      return;
    }
    const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
    const mr = new MediaRecorder(stream);
    mediaRecorderRef.current = mr;
    recordedChunksRef.current = [];
    mr.ondataavailable = e => { if (e.data.size > 0) recordedChunksRef.current.push(e.data); };
    mr.onstop = async () => {
      const blob = new Blob(recordedChunksRef.current, { type: 'audio/webm' });
      const file = new File([blob], `voice-${Date.now()}.webm`, { type: 'audio/webm' });
      handleFiles([file]);
    };
    mr.start();
    setRecording(true);
  };
  const stopRecording = () => {
    mediaRecorderRef.current?.stop();
    setRecording(false);
  };

  // Admin connect (placeholder - initiate special admin conversation or call)
  const connectAdmin = async () => {
    setIsConnectingAdmin(true);
    // Replace with actual API to create/find admin conversation
    setTimeout(() => {
      const adminConv = conversations.find(c => c.id === 'conv_admin');
      if (adminConv) setActiveConvId(adminConv.id);
      else {
        const newConv = { id: 'conv_admin', name: 'Admin', last: 'You connected to admin', unread: 0 };
        setConversations(prev => [newConv, ...prev]);
        setActiveConvId(newConv.id);
      }
      setIsConnectingAdmin(false);
    }, 700);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto p-4">
        <div className="flex items-center justify-between mb-4">
          <h1 className="text-2xl font-bold text-gray-800 flex items-center gap-2"><MessageCircle className="w-6 h-6 text-blue-600"/> Messages</h1>
          <div className="flex items-center gap-3">
            <button onClick={connectAdmin} className="px-3 py-2 bg-blue-600 text-white rounded-md shadow-sm hover:bg-blue-700 transition flex items-center gap-2">
              <User className="w-4 h-4"/> Connect Admin
            </button>
            <Link href="/" className="text-sm text-gray-600 hover:underline">Go Home</Link>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-md overflow-hidden grid grid-cols-12">
          {/* Left: Conversations */}
          <aside className="col-span-12 md:col-span-3 border-r p-3 h-[70vh] overflow-auto">
            <div className="flex items-center justify-between mb-3">
              <h3 className="text-sm font-semibold text-gray-700">Conversations</h3>
              <button onClick={() => { /* new conv */ }} className="text-xs text-blue-600">New</button>
            </div>

            {loadingConvs ? (
              <p className="text-sm text-gray-400">Loading...</p>
            ) : (
              conversations.map(conv => (
                <div key={conv.id} onClick={() => setActiveConvId(conv.id)} className={`p-2 rounded-lg cursor-pointer mb-2 ${activeConvId === conv.id ? 'bg-blue-50 border border-blue-100' : 'hover:bg-gray-50'}`}>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center text-gray-600">{conv.name.split(' ').map(s=>s[0]).slice(0,2).join('')}</div>
                      <div>
                        <div className="text-sm font-medium text-gray-700">{conv.name}</div>
                        <div className="text-xs text-gray-500">{conv.last}</div>
                      </div>
                    </div>
                    <div className="text-xs text-gray-500">{conv.unread ? <span className="bg-red-500 text-white px-2 py-0.5 rounded-full text-xs">{conv.unread}</span> : null}</div>
                  </div>
                </div>
              ))
            )}
          </aside>

          {/* Center: Chat Window */}
          <section className="col-span-12 md:col-span-6 p-3 flex flex-col h-[70vh]">
            <div className="flex items-center justify-between p-2 border-b">
              <div>
                <div className="text-sm font-semibold text-gray-700">{conversations.find(c=>c.id===activeConvId)?.name || 'Select a conversation'}</div>
                <div className="text-xs text-gray-500">Online</div>
              </div>
              <div className="flex items-center gap-2">
                <button title="Call" onClick={() => makeCall()} className="p-2 rounded-md hover:bg-gray-100"><Phone className="w-4 h-4"/></button>
                <button title="Video Call" onClick={() => alert('Initiate video call (placeholder)')} className="p-2 rounded-md hover:bg-gray-100"><Video className="w-4 h-4"/></button>
                <button title="Send SMS" onClick={() => sendSms()} className="p-2 rounded-md hover:bg-gray-100"><Phone className="w-4 h-4 rotate-90"/></button>
              </div>
            </div>

            <div className="flex-1 overflow-auto p-3" style={{ background: 'linear-gradient(180deg,#f8fafc,transparent)' }}>
              {activeConvId ? (
                messages.length === 0 ? (
                  <p className="text-center text-gray-400 mt-8">No messages yet. Start the conversation.</p>
                ) : (
                  messages.map(msg => (
                    <div key={msg.id} className={`mb-3 flex ${msg.from === 'me' ? 'justify-end' : 'justify-start'}`}>
                      <div className={`${msg.from === 'me' ? 'bg-blue-600 text-white' : 'bg-gray-100 text-gray-800'} rounded-xl p-3 max-w-[80%]`}> 
                        {msg.text && <div className="mb-2">{msg.text}</div>}
                        {msg.attachments?.map((a, i) => (
                          <div key={i} className="mb-2">
                            {a.type === 'image' && <img src={a.preview} className="max-w-xs rounded-md" alt={a.name} />}
                            {a.type === 'video' && <video src={a.preview} controls className="max-w-xs rounded-md"/>}
                            {a.type === 'file' && <div className="flex items-center gap-2 bg-white p-2 rounded-md border"><FileText className="w-4 h-4"/> <div className="text-xs">{a.name}</div></div>}
                          </div>
                        ))}
                        <div className="text-right text-[11px] text-gray-200/80 mt-1">{msg.time}</div>
                      </div>
                    </div>
                  ))
                )
              ) : (
                <div className="h-full flex items-center justify-center text-gray-400">Select a conversation from left</div>
              )}
              <div ref={messagesEndRef} />
            </div>

            {/* Composer */}
            <div className="mt-3 border-t pt-3">
              <div className="flex items-center gap-2">
                <button onClick={() => fileInputRef.current.click()} className="p-2 rounded-md hover:bg-gray-100"><Paperclip className="w-5 h-5"/></button>
                <input ref={fileInputRef} type="file" className="hidden" multiple onChange={(e) => handleFiles(e.target.files)} />

                <div className="flex-1">
                  <textarea value={text} onChange={(e) => setText(e.target.value)} rows={2} className="w-full p-2 rounded-md border resize-none" placeholder="Write a message..."></textarea>
                  {/* attachments preview */}
                  <div className="flex gap-2 mt-2 overflow-auto">
                    {attachments.map(att => (
                      <div key={att.id} className="relative border rounded-md p-1 bg-white">
                        {att.type === 'image' && <img src={att.preview} className="w-24 h-24 object-cover rounded-md" alt="att" />}
                        {att.type === 'video' && <video src={att.preview} className="w-24 h-24 object-cover rounded-md"/>}
                        {att.type === 'file' && <div className="flex flex-col items-start p-2"><FileText className="w-6 h-6"/><div className="text-xs mt-1">{att.file.name}</div><div className="text-xs text-gray-400">{readableSize(att.file.size)}</div></div>}
                        <button onClick={() => removeAttachment(att.id)} className="absolute top-1 right-1 bg-white p-1 rounded-full shadow-sm"><Trash2 className="w-3 h-3"/></button>
                      </div>
                    ))}
                  </div>
                </div>

                {/* Voice record */}
                <div className="flex items-center gap-2">
                  {!recording ? (
                    <button onClick={startRecording} className="p-2 rounded-md hover:bg-gray-100"><Mic className="w-5 h-5"/></button>
                  ) : (
                    <button onClick={stopRecording} className="p-2 bg-red-500 text-white rounded-md">Stop</button>
                  )}

                  <button onClick={sendMessage} className="px-3 py-2 bg-green-600 text-white rounded-md flex items-center gap-2"><Send className="w-4 h-4"/> Send</button>
                </div>
              </div>
            </div>
          </section>

          {/* Right: Admin / Info */}
          <aside className="hidden md:block col-span-3 border-l p-3 h-[70vh] overflow-auto">
            <div className="mb-4">
              <h4 className="text-sm font-semibold">Conversation Details</h4>
              <div className="p-3 bg-gray-50 rounded-md">
                <div className="text-xs text-gray-500">Trust Market – আপনার নিরাপদ ও সহজ অনলাইন মার্কেটপ্লেস অ্যাপ
Trust Market একটি আধুনিক অ্যাপ যা ব্যবহারকারীদের জন্য নিরাপদ, সহজ এবং সুবিধাজনকভাবে অনলাইন কেনাবেচার সুযোগ করে দেয়।

প্রধান ফিচারসমূহ:
নিরাপদ লেনদেন
সহজ ন্যাভিগেশন
বিক্রেতাদের জন্য সুবিধা
রিয়েল টাইম আপডেট
বিশ্বাসযোগ্য কমিউনিটি</div>
              </div>
            </div>

          </aside>
        </div>

        <div className="text-xs text-gray-400 mt-2">Note: This is a UI template. Replace placeholders with real API calls/websocket logic and secure admin endpoints.</div>
      </div>
    </div>
  );
}
